/* Transaction Details */
.bodyStyles {
	background: var(--bg-light);
	margin-bottom: 0 !important;
}
.transaction__summary {
	border-radius: 4px;
	padding: 24px 16px;
	margin-bottom: 32px;
	background: var(--bg-dark);
}

.tag {
	border-radius: 4px;
	font-size: 12px;
	height: 64px;
	line-height: 24px;
	text-align: center;
	margin-right: 15px;
	width: 64px;
	height: 24px;
	background-color: rgb(16 147 245 / 10%);
	color: #1093F5;
}

.tag__ach {
	background-color: #1093F51A;
	color: #1093F5;
}

.tag__intrabank {
	background-color: #5C33E61A;
	color: #5C33E6;
}
.tag__card {
	background-color: #1AAEC71A;
	color: #1AAEC7;
}

.transaction__description {
	margin-right: auto;
}

.transaction__received {
	color: #2EC193;
	font-weight: 500;
}

.transaction__sent {
	color: #FF4A4A;
	font-weight: 500;
}

.transaction__other_details {
	border-radius: 4px;
	padding: 12px 16px;
	margin: 16px 0 32px;
	background: var(--bg-dark);
}

.actions__action {
	border-radius: 4px;
	line-height: 48px;
	flex: 1;
	text-align: center;
	justify-content: center;
	font-weight: 500;
	cursor: pointer;
	background: var(--bg-dark);
	box-shadow: none;
	color: var(--link-text);
	border: 1px solid var(--border-color-1);
}

.actions__action:hover {
	background: var(--bg-dark);
	box-shadow: none;
	color: var(--link-text);
	border: 1px solid var(--border-color-1);
}

/* Attachments */
.attachments {
	display: flex;
	flex-wrap: wrap;
	gap: 18px;
	margin-bottom: 32px;
}
.attachment__container {
	flex: 40%;
}

.attachment__container__full_width {
	flex: 100%;
	text-align: center;
}

.attachment {
	background-color: var(--bg-dark);
	border-radius: 4px;
	padding: 8px;
	min-height: 200px;
}

.attachment__img_container {
	background-color: var(--bg-dark);
	display: flex;
	justify-content: center;
}

.attachment__img {
	padding: 16px;
}

/* Location */
.location__container {
	background-color: var(--bg-dark);
	border-radius: 4px;
	padding: 8px;
}

.location__map {
	background-color: rgb(219, 219, 219);
	border-radius: 4px;
	height: 170px;
}

.location__info {
	padding: 24px 16px;
	border-bottom: 1px solid var(--border-color-1);
}

.location__info:last-of-type {
	border: 0;
}